.navigation {
    display: flex;              /* establish flex container */
    flex-direction: column;     /* stack flex items vertically */
    justify-content: center;    /* center items vertically, in this case */
    align-items: center;        /* center items horizontally, in this case */
}

.navbar {
    display: flex;
    justify-content: space-around;
    padding: 16px;
}

.search-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Styling the search input */
.search-input {
    width: 420px; 
    
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}

.search-input:focus {
    border-color: #515151;
}

/* Styling the search button */
.search-button {
    width: 80px;
    height: 58px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

.search-button:hover {
    background-color: #515151;
}

/* Styling the SVG icon */
.search-button svg {
    width: 20px;
    height: 20px;
    color: #fff;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button {
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.nav-login-cart button:active {
    background: #f1f1f1; 
}

.nav-cart-count {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-logo p {
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}

.navbar-bottom {
    position: relative;
    z-index: 1;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2px;
    cursor: pointer;
}

.nav-menu hr {
    border: none;
    width: 80%;
    height:3px;
    border-radius: 10px;
    background: #FF4141;
}