.hero {
    height: 100vh;
    background: linear-gradient(180deg, #efda22, #ffffff 90%);
    display: flex;
}

.hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}

.hero-left p {
    color: #171717;
    font-size: 60px;
    font-weight: 700;
}

.hero-latest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 280px;
    height: 60px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 180px;
}

.hero-right img {
    width: 120vh;
    height: 52vh;
}